div{box-sizing:border-box}p{margin:0;text-align:start}input{margin:0;outline:0;line-height:1rem;padding:.3rem;font-size:1rem;background:#fff;border:1px solid #add8e6;color:rgba(0,0,0,.8);border-radius:8px;box-shadow:0 0 0 0 rgba(0,0,0,0)}.App{min-height:100vh;display:flex;flex-direction:column;margin:0 auto;padding:20px;align-items:center;color:#000;background-color:#e0ffff;text-align:center;font-size:1rem;transition:all .5s;box-sizing:border-box}.container{width:480px}@media(max-width: 520px){.container{width:100%}}.section{margin-bottom:10px}.btn{text-wrap:nowrap;cursor:pointer;border:none;background:rgba(0,0,0,0);padding:0;line-height:0;font-weight:700;border-radius:5px}.btn[disabled]{opacity:.9}.btn_icon{font-size:1rem;color:#3988a2}.btn_icon:hover{color:#2b687c}.btn_icon___lg{font-size:2rem}.field{border-radius:5px;padding:.4rem}.checkbox{appearance:none;width:1rem;height:1rem;border-radius:3px;border:3px solid #3988a2;outline:none;cursor:pointer}.checkbox:focus{box-shadow:0 0 5px #3988a2}.checkbox:checked{background-color:#3988a2;position:relative}.checkbox:checked::before{content:"âœ”";font-size:1rem;color:#fff;position:absolute;right:-2px;top:-2px}.text:first-letter{text-transform:uppercase}.loader{margin:0 auto;display:flex;justify-content:center;align-items:center;flex:1 0 auto;background:var(--secondary-bg)}.loader_spinner{width:30px;height:30px;border:4px solid;border-color:#3d5af1 rgba(0,0,0,0) #3d5af1 rgba(0,0,0,0);border-radius:50%;animation:spin-anim 1.2s linear infinite}@keyframes spin-anim{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.pagination{margin:30px auto;padding:0;display:flex;align-items:center;justify-content:center;list-style:none;outline:none}.pagination>.active>a{background-color:#000;border-color:#000;color:#fff}.pagination>li>a{border:1px solid #000;padding:5px 10px;outline:none;cursor:pointer}